.App {
  text-align: center;
}
body{
  background-color: rgb(42, 218, 182);
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-link {
  color: #61dafb;
}

.mainFile{
  font-size: 30px;
  text-align: center;
  margin: 0;
  color: rgb(255, 255, 255);
  margin-bottom: 15px;
  background-color: rgba(50, 39, 39, 0.7);
  border-radius: 15px;
  padding: 5px;
}

.appBlock{
  color: white;
  display: flexbox;
  font-family: tahoma;
  letter-spacing: 1.5px;
  font-size: 20px;;
  max-width: 77vw;
  min-height: 85vh;
  margin: 15px auto;
  padding: 20px;
  background: rgba(50, 39, 39, 0.7);
  border-radius: 15px;
  box-shadow: 5px 10px #000000;
}

input{
  width: 100%;
  margin-bottom: 15px;
  border-radius: 5px;
}

.submitBtn{
  width: 100px;
  height: 25px;
  border: none;
  outline: none;
  background: #ffffff;
  color: #000000;
  font-size: 15px;
  border-radius: 40px;
  text-align: center;
  box-shadow: 0 6px 20px -5px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.disable{
  opacity: 0.4;
  cursor: not-allowed;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
